//main styles
.main-wrapper {
    padding: 0;
    min-width: $min-width;
    width: 100%;
    position: relative;
    min-height: 100%;
    opacity: 1;
    transition: 1s;
    padding: 90px 0 0;
    overflow: hidden;
}

.wrapper {
    min-width: $min-width;
    max-width: $max-width;
    padding: 0 20px;
    margin: 0 auto;
    position: relative;
}

p {
    padding: 0 0 15px;
}

/* titles start */
h1 {
    font-family: $custom-font-family;
    font-size: $font-size-h1;
    line-height: $line-height-h1;
    padding: 0 0 15px;
    color: $title-color;
}

h2 {
    font-family: $custom-font-family;
    font-size: $font-size-h2;
    line-height: $line-height-h2;
    padding: 0 0 15px;
    flex-direction: flex-start;
    -webkit-box-orient: vertical;
    color: $title-color;
}

h3 {
    font-family: $custom-font-family;
    font-size: $font-size-h3;
    line-height: $line-height-h3;
    padding: 0 0 15px;
    color: $title-color;
}

h4,
h5,
h6 {
    font-weight: 700;
    padding: 0 0 15px;
    color: $title-color;
}

/* titles end */
.custom2 {
    font-family: $custom2-font-family;
}

/* Styles for dialog window */
.small-popup {
    background: #fff;
    padding: 20px 30px;
    text-align: left;
    max-width: 400px;
    margin: 40px auto;
    position: relative;
}

.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
    opacity: 0;
    -webkit-backface-visibility: hidden;
    /* ideally, transition speed should match zoom duration */
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
    opacity: 1;
}

.mfp-with-zoom.mfp-ready.mfp-bg {
    opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
    opacity: 0;
}

/* Styles for dialog window end */
.mm-opened .mm-slideout {
    cursor: pointer;
}

.mm-title img {
    height: 20px;
}

.slick-arrow {
    position: absolute;
    top: 32%;
    border: none;
    cursor: pointer;
    padding: 0;
    width: 24px;
    height: 24px;
    z-index: 3;

    &.slick-prev {
        left: 10px;
    }

    &.slick-next {
        right: 10px;
    }
}

.mm-page,
#my-page {
    height: 100%;
}

.icon {
    display: inline-block;
}

/* loader */
.loaded .main-wrapper {
    visibility: hidden;
    opacity: 0;
}

.icon-load {
    /*background: url(../img/loader.svg) no-repeat left top;*/
    width: 100px;
    height: 100px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    display: none;

    img {
        width: 100%;
        height: 100%;
    }
}

.loaded .icon-load {
    display: block;
}

.hidden-block {
    display: none;
}

.sticky-holder {
    z-index: 99;
    padding: 18px 15px 14px;
    transition: .2s;

    &.fup {
        transform: translateY(-90px);
    }

    &.fixed {
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        background-color: $link-color;
        transform: translateY(0);
    }
}

.box-tab-cont {
    position: relative;
    z-index: 2;
    overflow: hidden;
}

.tab-cont {
    transition: .2s;
    max-height: 400px;
}

.hide-tab {
    max-height: 0;
    overflow: hidden;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-40px);
    position: relative;
}

.crop-block {
    padding: 22px;
    border: 1px solid #fff;
    margin: 0 22px;
}

.hamburger {
    width: 39px;
    height: 32px;
    padding: 0;

    &-box {
        width: 100%;
        height: 100%;
        position: relative;
        opacity: 1;
    }

    &:hover {
        opacity: 1;
    }
}

.hamburger-inner,
.hamburger-inner:before,
.hamburger-inner:after {
    width: 100%;
    height: 6px;
    background-color: #6c6c6c;
    border-radius: 4px;
}

.hamburger-inner:before {
    top: -13px;
}

.hamburger-inner:after {
    bottom: -13px;
}

.mm-menu .mm-navbar a,
.mm-menu .mm-navbar>* {
    color: #666;
}

.mm-navbar {
    font-size: 12px;
    color: #999 !important;
    opacity: 1 !important;

    .phone {
        a {
            color: $link-color !important;
            font-weight: bold;
        }

        div {
            font-size: 10px;
        }
    }
}

.header {
    background: #fff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 98;

    .wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        max-width: 100%;
        min-width: 100px;
        width: 100%;
        padding: 19px 25px 18px;
        height: 90px;
    }

    nav {
        display: none;
    }

    .email_phone {
        display: none;
    }

    .logo {
        width: 134px;
        height: 37px;

        img {
            display: none;
            width: 100%;
            height: 100%;

            &.logo_mobile {
                display: block;
            }
        }
    }
}

.visual {
    height: 441px;
    background: url(../img/visual-mobile-bg.png) no-repeat;
    background-size: 100% 100%;

    &__text {
        padding: 70px 0 0 10px;
    }

    h1 {
        color: #6c6c6c;
        font: 700 21px/32px 'Montserrat', sans-serif;
        padding-bottom: 20px;

        span {
            display: inline-block;
            font: bold 26px/32px 'Montserrat', sans-serif;
            color: #2B2A29;
            position: relative;
            padding: 0 5px;

            &:before {
                content: '';
                display: block;
                width: 100%;
                height: 14px;
                border-radius: 7px;
                position: absolute;
                bottom: 2px;
                background-color: $link-color-hover;
                left: 0;
            }
        }

        strong {
            position: relative;
        }
    }

    p {
        font: 600 12px/18px 'Montserrat', sans-serif;
        padding: 0 15px 22px 0;
        color: #666;

        strong {
            color: #2B2A29;
            font-weight: bold;
        }
    }

    .green_label {
        width: 323px;
        height: 98px;
        background: url(../img/green-label.svg) no-repeat;
        background-size: 100% 100%;
        color: #fff;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 0 0 4px 15px;

        h2 {
            padding: 0;
            color: #fff;
            font: bold 15px/18px 'Montserrat', sans-serif;
        }

        .subtitle {
            font: 600 12px/15px 'Montserrat', sans-serif;
        }
    }
}

.content {
    min-width: $min-width;
}

.scheme1 {
    margin-top: -40px;

    &__half {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        padding: 0 0 0 10px;
    }

    .img_box {
        width: 54%;
        order: 2;
        text-align: right;
    }

    ul {
        width: 40%;
        margin: 0;
        padding: 0;
    }

    li {
        list-style: none;
        padding: 7px 0 7px;

        .title {
            padding-bottom: 4px;
            font: 700 13px/16px 'Montserrat', sans-serif;
            color: #2B2A29;
        }

        .ico {
            position: relative;
            width: 45px;
            height: 45px;

            img {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
    }

    .ico_box {
        position: relative;

        &:after {
            width: calc(100% - 52px);
            height: 5px;
            content: '';
            display: block;
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background: url(../img/dots-hor.png) repeat-x right;
        }

        &:before {
            content: '';
            display: block;
            height: 5px;
            width: 8px;
            background: linear-gradient(to right, rgba(250, 250, 250, 1) 0%, rgba(255, 255, 255, 0) 100%);
            position: absolute;
            z-index: 2;
            left: 52px;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    .scheme1__half_second {
        padding: 0 10px 0 0;
        text-align: right;

        .img_box {
            order: 1;
            text-align: left;
        }

        ul {
            order: 2;
        }

        li {
            .ico {
                margin-left: auto;
            }
        }

        .ico_box {
            &:after {
                right: auto;
                left: 0;
                background-position: left center;
            }

            &:before {
                left: auto;
                right: 52px;
                background: linear-gradient(to right, rgba(250, 250, 250, 0) 0%, rgba(255, 255, 255, 1) 100%);
            }
        }
    }
}

.scheme1__item {
    display: none;
}

.right_links {
    display: none;
    z-index: 12;
}

.pretty_title {
    text-align: center;
    font: bold 28px/34px 'Montserrat', sans-serif;
    text-transform: uppercase;
    padding: 0 0 2px;
    color: #2B2A29;

    &:first-letter {
        color: #00924F;
    }

    span {
        color: #0B56A4;
    }
}

.pt_subtitle {
    font: 500 13px/16px 'Montserrat', sans-serif;
    text-align: center;
    text-transform: lowercase;
    color: #6C6C6C;
    padding-bottom: 40px;
}

.catalog {
    background-color: #f8f8f8;
    background: linear-gradient(to bottom, rgba(248, 248, 248, 1) 0%, rgba(255, 255, 255, 1) 100%);
    position: relative;
    min-height: 300px;
    margin-top: 40px;

    &:before {
        content: '';
        position: absolute;
        top: -60px;
        width: 0;
        height: 0;
        border-bottom: 60px solid #f8f8f8;
        border-right: 100vw solid transparent;
    }

    .wrapper {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    &__list {
        position: relative;
        z-index: 2;
        &_item {
            padding: 0 0 22px;
        }

        a {
            display: block;
            text-decoration: none !important;
        }

        .img_box {
            padding: 0 20px 15px 0;

            img {
                display: block;
            }
        }

        h3 {
            color: #2B2A29;
            font: bold 18px/22px 'Montserrat', sans-serif;
            text-align: right;
            padding: 0 0 5px 0;
        }

        .line {
            width: 129px;
            height: 5px;
            background-color: #00924F;
            margin: 0 0 5px auto;
        }

        p {
            font: 12px/15px 'Montserrat', sans-serif;
            color: #6C6C6C;
            text-align: right;
            padding: 0 0 10px 0;
        }
    }
}

.catitem__text {
    padding-right: 45px;
}

.advantages {
    padding: 10px 0 20px;
    background-color: #fff;

    .pretty_title {
        padding-bottom: 31px;
    }
}

.avd__wrapper {
    position: relative;
    z-index: 7;
}

.avd {
    padding: 22px 23px;
