}

.wave {
    width: 100%;
    height: 94px;
    background: url(../img/wave-mobile.png) no-repeat;
    background-size: 100% 100%;
}

.difference {
    background-color: #f8f8f8;
    background: linear-gradient(to bottom, rgba(248, 248, 248, 1) 0%, rgba(255, 255, 255, 1) 100%);
