}

.sub_h3 {
    color: #6c6c6c;
    text-align: center;
    font: 14px/32px 'Montserrat', sans-serif;
    margin: -25px 0 25px;
}

.tech {
    background-color: #f8f8f8;
    background: linear-gradient(to bottom, #f8f8f8 0%, white 100%);
    position: relative;
    padding: 1px 0;

    &:before {
        content: '';
        position: absolute;
        top: -113px;
        width: 0;
        height: 0;
        border-bottom: 113px solid #f8f8f8;
        border-left: 100vw solid transparent;
    }

    .pretty_title {
        margin-top: -80px;
    }
}

.five_boxes {
    height: 960px;
    position: relative;
}

.box_green {
    position: absolute;
    left: 50%;
    margin: 0 0 0 -45%;
    width: 144px;
    height: 208px;
    background: url(../img/box-green.png) no-repeat center;
    background-size: 100% 100%;
    top: 0;
    padding: 38px 7px 0 20px;
    font: 12px/15px 'Montserrat', sans-serif;
    color: #6C6C6C;

    &:after {
        content: '';
        display: block;
        width: 69px;
        height: 76px;
        background: url(../img/arrow-green.svg) no-repeat;
        position: absolute;
        top: 100%;
        margin-top: 15px;
        left: 50%;
        transform: translateX(-50%);
    }

    &.bgr2 {
        margin: 0 -45% 0 0;
        left: auto;
        right: 50%;
    }
}

.box_blue {
    background: #fff;
    border: 10px solid #5795D3;
    box-sizing: border-box;
    border-radius: 30px;
    width: 144px;
    height: 223px;
    top: 314px;
    left: 50%;
    margin-left: -45%;
    position: absolute;
    padding: 20px 12px 0;
    text-align: center;
    font: 12px/15px 'Montserrat', sans-serif;
    color: #6C6C6C;
    &.bbl2 {
        margin-left: 0;
        margin-right: -45%;
        left: auto;
        right: 50%;
        &:before {
            display: none;
        }
    }

    &:before {
        content: '';
        display: block;
        width: 69px;
        height: 76px;
        background: url(../img/arrow-green.svg) no-repeat;
        position: absolute;
        top: 100%;
        margin-top: 19px;
        left: 50%;
        transform: rotate(-45deg) translateX(-50%);
    }
}

.micro {
    position: absolute;
    top: 636px;
    left: 50%;
    transform: translateX(-50%);
    width: 223px;
    height: 223px;
    &:after {
        content: '';
        display: block;
        width: 69px;
        height: 77px;
        background: url(../img/arrow-red.svg) no-repeat;
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 5px;
    }
}

.sphere {
    width: 266px;
    margin: 0 auto 36px;
    position: relative;
    .title {
        font: bold 13px/16px 'Montserrat', sans-serif;
        color: #000;
        padding-bottom: 9px;
        text-transform: uppercase;
    }
    p {
        font: 500 11px/13px 'Montserrat', sans-serif;
        padding: 0;
        color: #000;
        span {
            color: #00924F;
        }
    }
    .sphere_img {
        position: absolute;
        top: 155px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        width: 266px;
        height: 266px;
        background: url(../img/sphere.png) no-repeat;
    }
    &__top {
        height: 290px;
        position: relative;
        margin: 0 47px 19px 27px;
        &:before {
            content: '';
            display: block;
            background-color: #2b2a29;
            height: 100%;
            width: 3px;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
    &__top_1 {
        padding-left: 11px;
        height: 116px;
    }
    &__top_2 {
        height: 105px;
        position: relative;
        z-index: 2;
        text-align: right;
        padding-right: 11px;
        &:before {
            width: 3px;
            height: 100%;
            content: '';
            display: block;
            right: 0;
            top: 0;
            position: absolute;
            background-color: #2b2a29;
        }
    }
    &__bottom {
        position: relative;
        z-index: 2;
        height: 143px;
        padding: 110px 0 0 11px;
        margin-left: 54px;
        &:before {
            content: '';
            display: block;
            background-color: #2b2a29;
            height: 100%;
            width: 3px;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
}

