.footer {
    height: 130px;
    width: 100%;
    background: #fff url(../img/footer-bg.png) no-repeat center;
    background-size: auto 130px;
    padding: 30px 0 0;

    p {
        font: 600 10px/12px 'Montserrat', sans-serif;
        text-align: center;
        color: #787878;
    }
}